import React from 'react';
import Unity, { UnityContext } from 'react-unity-webgl';

export default function UnityClickyGame() {
    const unityContext = new UnityContext({
        loaderUrl: "build//jumper//Jumper.loader.js",
        dataUrl: "build/jumper/Jumper.data",
        frameworkUrl: "build/jumper/Jumper.framework.js",
        codeUrl: "build/jumper/Jumper.wasm",
    });
    return <Unity
        unityContext={unityContext}
        style={{
            height: "100%",
            width: "100%",
            border: "2px solid black",
            background: "grey",
        }}
    />
}
