import React from 'react';
import Unity, { UnityContext } from 'react-unity-webgl';

export default function UnityClickyGame() {
    const unityContext = new UnityContext({
        loaderUrl: "build/clicky/Build.loader.js",
        dataUrl: "build/clicky/Build.data",
        frameworkUrl: "build/clicky/Build.framework.js",
        codeUrl: "build/clicky/Build.wasm",
      });
    return <Unity
        unityContext={unityContext}
        style={{
            height: "100%",
            width: 950,
            border: "2px solid black",
            background: "grey",
        }}
    />
}
